.content-article{
  display: grid;
  grid-template-columns: 200px 335px;
  grid-gap: .5em;
  @include media-query("small"){
    grid-template-columns: 120px 325px;
  }

  figure{
    border-radius: 8px;
    overflow: hidden;
  }

  main{
    display: grid;
    grid-template-rows: 40px auto 16px;
    background-color: khaki;
  }
}
